import React, {Component} from 'react';
import {ErrorMessage, Field, Form, Formik} from "formik";
import * as Yup from "yup";
import {ExchangeService} from '../services'

export class ExchangeTransactionAdd extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        return (
            <div>
                <h1>Add a new transaction</h1>
                <Formik
                    initialValues={{
                        amount: '',
                        currency: 'AED'

                    }}
                    validationSchema={Yup.object().shape({
                        amount: Yup.string().required('Amount is required')

                    })}
                    onSubmit={(request, {setStatus, setSubmitting}) => {
                        console.log(request);
                        ExchangeService.addExchange().then(
                            user => {
                                const {from} = this.props.location.state || {from: {pathname: "/exchangeTransactions"}};
                                this.props.history.push(from);
                            },
                            error => {
                                setSubmitting(false);
                                let sb = "";
                                for (let key in error) {
                                    if (error.hasOwnProperty(key)) {
                                        sb += key + ": " + error[key];
                                    }
                                }

                                setStatus(sb);
                            }
                        );
                    }}
                        >
                    {({errors, status, touched, isSubmitting}) => (
                        <Form>

                    {status &&
                        <div className={'alert alert-danger'}>{status}</div>
                    }

                        <div className="form-group">
                        <label htmlFor="amount">Amount</label>
                        <Field name="amount" type="number"
                        className={'form-control' + (errors.amount && touched.amount ? ' is-invalid' : '')}/>
                        <ErrorMessage name="amount" component="div" className="invalid-feedback"/>
                        </div>

                        <div className="form-group">
                        <label htmlFor="currency">Curency</label>

                        <Field name="currency" as="select"
                        className={'form-control' + (errors.currency && touched.currency ? ' is-invalid' : '')}>
                        <option value="AED">AED</option>
                        <option value="AUD">AUD</option>
                        <option value="BGN">BGN</option>
                        <option value="BRL">BRL</option>
                        <option value="CAD">CAD</option>
                        <option value="CHF">CHF</option>
                        <option value="CNY">CNY</option>
                        <option value="CZK">CZK</option>
                        <option value="DKK">DKK</option>
                        <option value="EGP">EGP</option>
                        <option value="EUR">EUR</option>
                        <option value="GBP">GBP</option>
                        <option value="HRK">HRK</option>
                        <option value="HUF">HUF</option>
                        <option value="INR">INR</option>
                        <option value="JPY">JPY</option>
                        <option value="KRW">KRW</option>
                        <option value="MDL">MDL</option>
                        <option value="MXN">MXN</option>
                        <option value="NOK">NOK</option>
                        <option value="NZD">NZD</option>
                        <option value="PLN">PLN</option>
                        <option value="RSD">RSD</option>
                        <option value="RUB">RUB</option>
                        <option value="SEK">SEK</option>
                        <option value="THB">THB</option>
                        <option value="TRY">TRY</option>
                        <option value="UAH">UAH</option>
                        <option value="USD">USD</option>
                        <option value="XAU">XAU</option>
                        <option value="XDR">XDR</option>
                        <option value="ZAR">ZAR</option>
                        </Field>

                        <ErrorMessage name="currency" component="div" className="invalid-feedback"/>
                        </div>

                        <div className="form-group">
                        <button type="submit" className="btn btn-primary" disabled={isSubmitting}>Add new
                        transaction
                        </button>
                    {isSubmitting &&
                        <img
                        src="data:image/gif;base64,"/>
                    }
                        </div>
                        </Form>
                        )}
                        </Formik>
                        </div>
                        );
                        }
                        }
