import React, {Component} from 'react';
import {Route} from 'react-router';
import {Layout} from './components/Layout';
import {Home} from './components/Home';
import {ExchangeRates} from './components/ExchangeRates';
import {Counter} from './components/Counter';
import {Login} from './components/Login';
import {User} from './components/User';
import {Register} from './components/Register';
import {UserView} from "./components/UserView";
import {PrivateRoute} from "./components/PrivateRoute";
import './custom.css'
import {ExchangeTransaction} from "./components/ExchangeTransaction";
import {ExchangeTransactionAdd} from "./components/ExchangeTransactionAdd";

export default class App extends Component {
    static displayName = App.name;

    render() {
        return (
            <Layout>
                <Route exact path='/' component={Home}/>
                <Route path='/counter' component={Counter}/>
                <PrivateRoute exact path='/exchangeRates' component={ExchangeRates}/>
                <PrivateRoute exact path='/exchangeTransactions' component={ExchangeTransaction}/>
                <PrivateRoute exact path='/exchangeTransactions/new' component={ExchangeTransactionAdd}/>
                <Route path='/login' component={Login}/>
                <PrivateRoute exact path='/user' component={User}/>
                <Route exact path='/register' component={Register}/>
                <PrivateRoute exact path='/user/:id' component={UserView}/>
            </Layout>
        );
    }
}
