import React, {Component} from 'react';
import {AuthenticationService} from '../services';
import {Card, CardBlock, CardHeader, CardText} from "reactstrap";


export class User extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        return (
            <div>
                <Card>
                    <CardHeader>Hello, {AuthenticationService.currentUserValue.user.firstName} {AuthenticationService.currentUserValue.user.lastName}!</CardHeader>
                    <CardText>
                        <ul className="list-group list-group-flush">
                            <li className="list-group-item">Username: {AuthenticationService.currentUserValue.user.userName}</li>
                            <li className="list-group-item">First
                                name: {AuthenticationService.currentUserValue.user.firstName}</li>
                            <li className="list-group-item">Last
                                name: {AuthenticationService.currentUserValue.user.lastName}</li>
                            <li className="list-group-item">Email: {AuthenticationService.currentUserValue.user.email}</li>
                        </ul>
                    </CardText>
                </Card>

            </div>
        );
    }
}
