import React, {Component} from 'react';
import {AuthHeader, HandleResponse} from "../helpers";
import moment from 'moment';
import {Button} from "reactstrap";
import {Link} from "react-router-dom";

export class ExchangeTransaction extends Component {

    constructor(props) {
        super(props);
        this.state = {exchangeTransactions: [], loading: true};
    }

    componentDidMount() {
        this.populateExchangeTransaction().then(result => {
            this.setState({exchangeTransactions: result, loading: false});
        });

    }

    static renderExchangeTransactionsTable(exchangeTransactions) {
        return (
            <table className='table table-striped' aria-labelledby="tableLabel">
                <thead>
                <tr>
                    <th>Date</th>
                    <th>Amount</th>
                    <th>Exchange Rate</th>
                    <th>Total</th>
                </tr>
                </thead>
                <tbody>
                {exchangeTransactions.map(exchangeTransaction =>
                    <tr key={exchangeTransaction.id}>

                        <td>
                            {moment(exchangeTransaction.date).format("dddd, MMM DD at HH:mm a")}
                        </td>
                        <td>
                            {exchangeTransaction.amount}
                        </td>
                        <td>{exchangeTransaction.exchangeRate.rate} {exchangeTransaction.exchangeRate.to}</td>
                        <td>{exchangeTransaction.exchangeRate.rate * exchangeTransaction.amount}</td>
                    </tr>
                )}
                </tbody>
            </table>
        );
    }

    render() {
        let contents = this.state.loading
            ? <p><em>Loading...</em></p>
            : ExchangeTransaction.renderExchangeTransactionsTable(this.state.exchangeTransactions);

        return (
            <div>
                <h1 id="tableLabel">Exchange rates</h1>
                <Link to="/exchangeTransactions/new">
                    Add
                </Link>
                {contents}
            </div>
        );
    }

    populateExchangeTransaction() {
        const requestOptions = {method: 'GET', headers: AuthHeader()};
        return fetch('api/ExchangeTransaction', requestOptions).then(HandleResponse);
    }
}
