import React, {Component} from 'react';
import {Card, CardHeader} from "reactstrap";

export class Home extends Component {
    static displayName = Home.name;

    render() {
        return (
            <div>
                <h1>Hello there,</h1>


                <p>This is an application that is aimed at exchange offices.</p>
                <p>It is an application to demonstrate some functionality in dotnet and it is far from production
                    ready.</p>

                <p>The application is a SPA written in React and dotnet.</p>
                <p>The application has:</p>
                <ul>
                    <li>Login and Register based on JWT;</li>
                    <li>Automatic addition of foreign exchange directly from the NBR;</li>
                    <li>Manually add transaction simulation (aka calculator);</li>
                    <li>View profiles;</li>
                    <li>Restriction to pages based on authentication / roles.</li>
                </ul>
            </div>
        );
    }
}
