import React, {Component} from 'react';
import {Collapse, Container, Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink} from 'reactstrap';
import {Link} from 'react-router-dom';
import './NavMenu.css';
import {AuthenticationService} from '../services';
import {History} from '../helpers';

export class NavMenu extends Component {
    static displayName = NavMenu.name;

    constructor(props) {
        super(props);

        this.toggleNavbar = this.toggleNavbar.bind(this);
        this.state = {
            collapsed: true,
            currentUser: null
        };
    }

    toggleNavbar() {
        this.setState({
            collapsed: !this.state.collapsed
        });
    }

    componentDidMount() {
        AuthenticationService.currentUser.subscribe(x => this.setState({currentUser: x}));
    }

    logout() {
        AuthenticationService.logout();
        History.push('/login');
    }

    render() {
        const {currentUser} = this.state;
        return (
            <header>
                <Navbar className="navbar-expand-sm navbar-toggleable-sm ng-white border-bottom box-shadow mb-3" light>
                    <Container>
                        <NavbarBrand tag={Link} to="/">ExchangeOffice</NavbarBrand>
                        <NavbarToggler onClick={this.toggleNavbar} className="mr-2"/>
                        <Collapse className="d-sm-inline-flex flex-sm-row-reverse" isOpen={!this.state.collapsed}
                                  navbar>
                            <ul className="navbar-nav flex-grow">
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark" to="/">Home</NavLink>
                                </NavItem>
                                {!currentUser &&
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark" to="/login">Login</NavLink>
                                </NavItem>
                                }
                                {currentUser &&
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark"
                                             to="/exchangeTransactions">Exchange Transactions</NavLink>
                                </NavItem>
                                }
                                {currentUser &&
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark"
                                             to="/exchangeRates">Exchange Rates</NavLink>
                                </NavItem>
                                }
                                {!currentUser &&
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark" to="/register">Register</NavLink>
                                </NavItem>
                                }
                                {currentUser &&
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark"
                                             to="/user">Hi, {AuthenticationService.currentUserValue.user.userName}</NavLink>
                                </NavItem>
                                }
                                {currentUser &&
                                <NavItem>
                                    <NavLink tag={Link} className="text-dark" onClick={this.logout}>Logout</NavLink>
                                </NavItem>
                                }
                            </ul>
                        </Collapse>
                    </Container>
                </Navbar>
            </header>
        );
    }
}
